@import "../variables.scss";

.product {
  max-width: $productMaxWidth;
  min-width: $productWidth;
  grid-row-start: auto;
  grid-column-start: auto;
  text-align: center;
  color: black;
  background-color: lightblue;

  display: flex;
  flex-direction: column;
  align-items: center;

  &__text {
    padding: 20px;
  }
  
  &__image {
    width: 90%;
    height: $productWidth;
    background-color: gray;

    img {
      width: 100%;
      height: 100%;
    }
  }

  &__heading {
    text-align: center;
    color: grayscale($color: #111111);
    padding: 10px;
    margin: 0;
    a {
      color: grayscale($color: #111111);;
    }
  }
}